@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
#scoreview {
  margin-top: 5px;
  height: 52px;
  margin-bottom: 3px;
  background: #FFE3B1;
  border: 3px solid #FFE3B1;
  border-radius: 8px;
  text-align: center;
}
#information {
  font-family: "NotoSansHans-Bold", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #F49E28;
  margin-bottom: 4px !important;
}
#significance {
  font-family: "NotoSansHans-Bold", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 11px;
  color: #F49E28;
}
/*********配置****/
.config-panel .fact-chart-preview {
  width: 500px;
  height: 500px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0;
  background: #FFFFFF;
  box-shadow: 2px 2px 10px 0 rgba(138, 138, 138, 0.5);
}
.config-panel .config-control {
  padding: 15px;
  padding-left: 4px;
}
.config-panel .config-control #caption::-webkit-scrollbar {
  display: hidden;
}
.config-panel .config-control #caption {
  font-family: "NotoSansHans-Bold", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 12px;
  color: #484F5D;
  line-height: 20px;
  height: 150px;
  background: #FFFFFF;
  border: 1px solid #484F5D;
  border-radius: 8px;
  padding: 6px;
  text-align: center;
}
.config-panel .config-control #caption .caption-img {
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/edit_black.png") no-repeat center;
  background-size: 10px 10px;
  width: 10px;
  height: 10px;
  background-color: transparent;
  position: absolute;
  top: 20px;
  right: 20px;
}
.config-panel .config-control #caption .caption-text {
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  overflow-y: scroll;
  width: 100%;
  height: 100%;
  font-size: 12px;
  line-height: 20px;
}
.config-panel .config-control #caption .caption-text textarea {
  background: transparent;
  resize: none;
}
.config-panel .config-control .config-control-detail {
  margin-top: 15px;
  height: 320px;
  overflow: auto;
}
.config-panel .config-control .config-control-detail .ant-row {
  margin-bottom: 4px;
  border: 1px solid #484F5D;
  border-radius: 8px;
  height: 32px;
}
.config-panel .config-control .config-control-detail .ant-row .ant-row {
  border: 0px;
}
.config-panel .config-control .config-control-detail .ant-select {
  border: none !important;
}
.config-panel .config-control .config-control-detail .ant-select-selector {
  border: 0px;
  background: transparent;
  color: #484F5D;
}
.config-panel .config-control .config-control-detail .ant-select-arrow {
  color: #484F5D;
}
.config-panel .config-control .config-control-detail .ant-select-arrow svg {
  width: 10px;
  height: 10px;
}
.config-panel .config-control .config-control-detail .ant-select-selection {
  border: none !important;
}
.config-panel .config-control .config-control-detail .select-box {
  width: 100%;
}
.config-panel .config-control .config-control-detail .ant-col,
.config-panel .config-control .config-control-detail .ant-select-selection-selected-value {
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 14px;
  color: #484F5D;
  line-height: 30px;
}
.config-panel .config-control .config-control-detail .ant-select-disabled .ant-select-selector {
  height: 29px;
  border-radius: 0px 8px 8px 0px;
}
.config-panel .config-control .config-control-detail .ant-col-8 {
  height: 32px;
  margin-top: -1px;
  display: -webkit-flex !important;
  display: flex !important;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: #484F5D;
  border-radius: 8px 1px 1px 8px;
  color: white;
}
.config-panel .config-control .config-control-detail .channelSlot button {
  font-size: 12px;
  position: relative;
  right: 6px;
  color: #484F5D;
}
.config-panel .config-control .config-control-detail .channelSlot button svg {
  width: 10px;
  height: 10px;
}
.config-panel .config-control .config-control-detail .channelSlot button:hover {
  color: #FFBE31;
}
.config-panel .config-control .config-control-detail #add-subspace {
  color: #484F5D;
  border: none;
  background: transparent;
  font-size: 16px;
  line-height: 0px;
  width: 100%;
}
.config-panel .config-control .config-control-detail #add-subspace:hover {
  color: #FFBE31;
}
.config-panel-vertical {
  width: 96vw;
}
.config-panel-vertical .fact-chart-preview {
  display: block;
  margin-left: 2vw;
  margin-top: 2vw;
  width: 92vw;
  height: 90vw;
}
.config-panel-vertical .fact-chart-preview div div {
  -webkit-transform-origin: center top;
          transform-origin: center top;
}
.config-panel-vertical .config-control {
  margin-top: 8px;
  padding: 2vw;
  padding-bottom: 15px;
}
.config-panel-vertical .config-control #caption .caption-img {
  top: calc(96vw + 50px);
}
.config-panel-vertical .config-control .config-control-detail {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
