@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.chartContainer:hover {
  border: dashed 1px #6D757A;
}
.chartContainer:hover .editButton {
  display: inherit;
}
.chartContainerDisabled:hover {
  border: dashed 1px rgba(0, 0, 0, 0);
}
.chartContainerDisabled:hover .editButton {
  display: none;
}
.chartContainer {
  border: dashed 1px rgba(0, 0, 0, 0);
  position: relative;
}
.chartContainer .editButton {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 20px;
  height: 20px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/edit_black.png") no-repeat center;
  background-size: 13px 13px;
  background-color: white;
  box-shadow: 0 1px 2px 0 rgba(155, 155, 155, 0.5);
  border-radius: 1px;
  display: none;
  cursor: pointer;
}
.chartEditor {
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
}
.chartEditor .ant-modal-content {
  box-shadow: 2px 2px 10px 0 rgba(138, 138, 138, 0.5);
  width: 770px;
  position: relative;
}
.chartEditor .ant-modal-content .ant-modal-header {
  background: #484F5D;
  height: 40px;
}
.chartEditor .ant-modal-content .ant-modal-header .ant-modal-title {
  color: #FFFFFF;
  line-height: 9px;
}
.chartEditor .ant-modal-content .ant-modal-body {
  height: 500px;
  padding: 0px;
}
.chartEditor .ant-modal-content .ant-modal-footer {
  position: absolute;
  top: 7px;
  right: 7px;
  border: 0px;
  height: 0px;
  padding: 0;
}
.chartEditor .ant-modal-content .ant-modal-footer .ant-btn {
  height: 25px;
  width: 80px;
  vertical-align: middle;
  line-height: 0px;
  border: 1px solid #979797;
  border-radius: 3px;
  background: transparent;
  color: white;
}
.chartEditor .ant-modal-content .ant-modal-footer .ant-btn:hover {
  border: 1px solid #FFBE31;
  color: #FFBE31;
}
.chartEditor .ant-modal-content .ant-modal-footer .ant-btn-primary {
  background: #FFBE31;
  color: #000000;
  border: 1px solid #FFBE31;
}
.chartEditor .ant-modal-content .ant-modal-footer .ant-btn-primary:hover {
  color: white;
}
@media screen and (max-width: 800px) {
  .chartEditor .ant-modal-content {
    width: 96vw;
  }
  .chartEditor .ant-modal-content .ant-modal-body {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
}
